    font-size: 65px;
    margin-left: 25px;
  }
  .weatherInfo .weatherCondition{
    text-transform: uppercase;
    font-size: 35px;
    font-weight: 100;
  }
  .weatherInfo .place{
    font-size: 15px;
  }
  .date{
      text-align: center;
      width:30% ;
      background: #70c1b3;
  display: flex;
  justify-content: space-around;
  align-items: center;
      color: white;
  font-size: 30px;
  font-weight: 800;
  }
  .weatherExtraInfo{
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
      width: 100%;
    background: #fafafa;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .twoSided{
      width: 25%;
      display: grid;  
      grid-template-columns: repeat(2, 1fr);
      text-align: center;
  align-items: center;
  color: #70c1b3;
  padding-right: 25px;
  }
  .twoSided .info{
    text-align: left;
    font-weight: 600;
    text-transform: capitalize;
    color: #080705;
    font-size: 15px;
  }

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
section
{
    display: grid;
    position: relative;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 20px;
    background: #001923;
    overflow: hidden;
}
section::before
{
    content: '';
    position: absolute;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    -webkit-transform: translate(-10px,-150px);
            transform: translate(-10px,-150px);
    background: linear-gradient(#ffeb3b,#e91e63);
}
section::after
{
    content: '';
    position:absolute;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    -webkit-transform: translate(970px,200px);
            transform: translate(970px,200px);
    background: linear-gradient(#2196f3,#83d8ff);
}
.container{
    position: relative;
    z-index: 10;
    max-width: 1000px;
    padding: 50px;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-right:1px solid rgba(255, 255, 255, 0.1) ;
    border-bottom:1px solid rgba(255, 255, 255, 0.1) ;
    border-radius: 10px;
    overflow: hidden;
    -webkit-backdrop-filter: blur(25px);
            backdrop-filter: blur(25px);
}
.container::before
{
    content:'';
    position: absolute;
    height: 100%;
    top: 0;
    right: -60%;
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    -webkit-transform: skewX(-15deg);
            transform: skewX(-15deg);
    pointer-events: none;
}
.container h2{
    width:100%;
    text-align: center;
    color:#fff;
    font-size: 36px;
    margin-bottom: 20px;
}
.container .row100
{
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(400px,1fr));
}
.container .row100 .col
{
    position: relative;
    padding: 0 10px;
    margin-top:30px;
    margin-bottom: 20px;
}
.container .row100 .col .inputBox
{
    position:relative;
    height: 40px;
    color: #fff;
}
.container .row100 .col .inputBox input,
.container .row100 .col .inputBox textarea
{
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    box-shadow: none;
    border: none;
    outline: none;
    font-size: 15px;
    padding: 0 10px;
    z-index: 1;
    color: #000;
    
}
.container .row100 .col .inputBox .text{
    position: absolute;
    top: 0;
    left: 0;
    line-height: 40px;
    font-size: 18px;
    padding: 0 10px;
    display: block;
    transition: 0.5s;
    pointer-events: none;
}
.container .row100 .col .inputBox input:focus + .text ,
.container .row100 .col .inputBox input:valid + .text,
.container .row100 .col .inputBox textarea:focus ~ .text ,
.container .row100 .col .inputBox textarea:valid ~ .text
{
    top: -35px;
    left: -10px;
}
.container .row100 .col .inputBox .line{
    position: absolute;
    bottom: 0;
    display: block;
    height: 2px;
    background: #fff;
    width: 100%;
    border-radius: 2px;
    pointer-events: none;
}
.container .row100 .col .inputBox input:focus ~ .line ,
.container .row100 .col .inputBox input:valid ~ .line
{
    height: 100%;
}
.textarea{
    position: relative;
    width: 100%;
    height: 100px!important;
    padding: 10px 0;
}
.container .row100 .col .inputBox textarea:focus ~ .line ,
.container .row100 .col .inputBox textarea:valid ~ .line
{
    height: 100%;
}
.container .row100 .col input[type="submit" ]
{
    border: none;
    padding: 10px 40px;
    cursor: pointer;
    outline: none;
    background-color: white;
    color: #000;
    font-weight: 600;
    font-size: 18px;
    border-radius: 2px;
}
.container .row100 .col input[type="submit" ]:hover{
    background: rgba(255, 255, 255, 0.8);
}

@media (max-width:768px){
    section::before
    {
        width: 300px;
        height: 300px;
        -webkit-transform: translate(-55px,-350px);
                transform: translate(-55px,-350px);
    }
    section::after{
        width: 300px;
        height: 300px;
        -webkit-transform: translate(250px,350px);
                transform: translate(250px,350px);
    }
}
