@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200&family=Poppins:ital,wght@0,200;0,300;0,500;0,600;0,700;1,100;1,400&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
section
{
    display: grid;
    position: relative;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 20px;
    background: #001923;
    overflow: hidden;
}
section::before
{
    content: '';
    position: absolute;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    transform: translate(-10px,-150px);
    background: linear-gradient(#ffeb3b,#e91e63);
}
section::after
{
    content: '';
    position:absolute;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    transform: translate(970px,200px);
    background: linear-gradient(#2196f3,#83d8ff);
}
.container{
    position: relative;
    z-index: 10;
    max-width: 1000px;
    padding: 50px;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-right:1px solid rgba(255, 255, 255, 0.1) ;
    border-bottom:1px solid rgba(255, 255, 255, 0.1) ;
    border-radius: 10px;
    overflow: hidden;
    backdrop-filter: blur(25px);
}
.container::before
{
    content:'';
    position: absolute;
    height: 100%;
    top: 0;
    right: -60%;
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    transform: skewX(-15deg);
    pointer-events: none;
}
.container h2{
    width:100%;
    text-align: center;
    color:#fff;
    font-size: 36px;
    margin-bottom: 20px;
}
.container .row100
{
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(400px,1fr));
}
.container .row100 .col
{
    position: relative;
    padding: 0 10px;
    margin-top:30px;
    margin-bottom: 20px;
}
.container .row100 .col .inputBox
{
    position:relative;
    height: 40px;
    color: #fff;
}
.container .row100 .col .inputBox input,
.container .row100 .col .inputBox textarea
{
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    box-shadow: none;
    border: none;
    outline: none;
    font-size: 15px;
    padding: 0 10px;
    z-index: 1;
    color: #000;
    
}
.container .row100 .col .inputBox .text{
    position: absolute;
    top: 0;
    left: 0;
    line-height: 40px;
    font-size: 18px;
    padding: 0 10px;
    display: block;
    transition: 0.5s;
    pointer-events: none;
}
.container .row100 .col .inputBox input:focus + .text ,
.container .row100 .col .inputBox input:valid + .text,
.container .row100 .col .inputBox textarea:focus ~ .text ,
.container .row100 .col .inputBox textarea:valid ~ .text
{
    top: -35px;
    left: -10px;
}
.container .row100 .col .inputBox .line{
    position: absolute;
    bottom: 0;
    display: block;
    height: 2px;
    background: #fff;
    width: 100%;
    border-radius: 2px;
    pointer-events: none;
}
.container .row100 .col .inputBox input:focus ~ .line ,
.container .row100 .col .inputBox input:valid ~ .line
{
    height: 100%;
}
.textarea{
    position: relative;
    width: 100%;
    height: 100px!important;
    padding: 10px 0;
}
.container .row100 .col .inputBox textarea:focus ~ .line ,
.container .row100 .col .inputBox textarea:valid ~ .line
{
    height: 100%;
}
.container .row100 .col input[type="submit" ]
{
    border: none;
    padding: 10px 40px;
    cursor: pointer;
    outline: none;
    background-color: white;
    color: #000;
    font-weight: 600;
    font-size: 18px;
    border-radius: 2px;
}
.container .row100 .col input[type="submit" ]:hover{
    background: rgba(255, 255, 255, 0.8);
}

@media (max-width:768px){
    section::before
    {
        width: 300px;
        height: 300px;
        transform: translate(-55px,-350px);
    }
    section::after{
        width: 300px;
        height: 300px;
        transform: translate(250px,350px);
    }
}